import {Component, Vue} from "vue-property-decorator";

import { namespace } from 'vuex-class';
const HomeModule = namespace('home');

@Component
export default class Search extends Vue {

  search: string = '';
  @HomeModule.Action('searchForBooks') searchForBooks: any;
  @HomeModule.Action('actClearRequestedBooks') actClearRequestedBooks: any;

  Search() {
    const bookTitle = this.search.trim();
    (bookTitle != '') ? this.searchForBooks(bookTitle) : this.actClearRequestedBooks();
  }

}
