import { Module, VuexModule, Mutation, Action } from 'vuex-module-decorators'

@Module({ namespaced: true })
class ChooseShelfModule extends VuexModule {

    currentBookID: String = '';

    get getCurrentBookID() { return this.currentBookID }

    @Mutation setCurrentBookID(bookID: String) {
        this.currentBookID = bookID
    }
    @Action actSaveCurrentBookID(bookID: String) {
        this.context.commit('setCurrentBookID', bookID);
    }
}
export default ChooseShelfModule; 