import { Module, VuexModule, Mutation, Action } from 'vuex-module-decorators'
import axios from 'axios'
import router from '../router/router';

@Module({ namespaced: true })

class GoogleBooksAPI extends VuexModule {

    ShelvesList = [];
    ShelfArray = [];
    CurrentShelf = 4;
    CurrentShelfName: string = '';
    CurrentShelfAccess: string = '';
    CurrentShelfPagesCount: number = 0;
    HaveReadBooksNumber: number = 0;
    HaveReadPagesNumber: number = 0;
    currentPage: any = 1;
    paginationLength: number = 0;
    savedBookFromSubjects: any = {};
    savedBookShown: boolean = false;

    Params: any = {
        URL: 'https://www.googleapis.com/books/v1/mylibrary/bookshelves',
        API_KEY: 'AIzaSyAeedIvfAxkWBPSiFMrQDQlcAZAf5BW1Gw',
        userID: '115700638400050674059',
        Scope: 'https://www.googleapis.com/auth/books',
        proxyurl: "https://cors-anywhere.herokuapp.com/",
    }

    get getShelvesList() { return this.ShelvesList }
    get getShelfArray() { return this.ShelfArray }
    get getCurrentShelf() { return this.CurrentShelf }
    get getCurrentShelfName() { return this.CurrentShelfName }
    get getCurrentShelfAccess() { return this.CurrentShelfAccess }
    get getCurrentShelfPagesCount() { return this.CurrentShelfPagesCount }
    get getHaveReadBooksNumber() { return this.HaveReadBooksNumber }
    get getHaveReadPagesNumber() { return this.HaveReadPagesNumber }
    get getCurrentPage() { return this.currentPage }
    get getPaginationLength() { return this.paginationLength }
    get getSavedBookFromSubjects() { return this.savedBookFromSubjects }
    get getSavedBookShown() { return this.savedBookShown }

    @Mutation setSavedBookShown(isSavedBookShown: boolean) {
        this.savedBookShown = isSavedBookShown;
    }
    @Mutation setSavedBookFromSubjects(bookFromSubjects: any) {
        this.savedBookFromSubjects = bookFromSubjects;
    }
    @Mutation setShelvesList(shelvesArr: any) {
        this.ShelvesList = shelvesArr;
    }
    @Mutation setShelfArray(shelfArray: any) {
        this.ShelfArray = shelfArray;
    }
    @Mutation setCurrentShelf(shelfId: any) {
        this.CurrentShelf = shelfId;
    }
    @Mutation setCurrentShelfName(shelfName: any) {
        this.CurrentShelfName = shelfName;
    }
    @Mutation setCurrentShelfAccess(Access: string) {
        this.CurrentShelfAccess = Access;
    }
    @Mutation setHaveReadBooksNumber(HaveReadBooks: number) {
        this.HaveReadBooksNumber = HaveReadBooks;
    }
    @Mutation setHaveReadPagesNumber(HaveReadPages: number) {
        this.HaveReadPagesNumber = HaveReadPages;
    }
    @Mutation setCurrentPage(currentPage: number) {
        this.currentPage = currentPage;
        this.ShelfArray = [];
    }
    @Mutation setPaginationLength(paginationLength: number) {
        this.paginationLength = paginationLength;
    }
    @Mutation setCurrentShelfPagesCount(pagesCount: number) {
        this.CurrentShelfPagesCount = pagesCount;
    }

    @Action actSavedBookFromSubjects(savedBookFromSubjects: any) {
        this.context.commit('setSavedBookFromSubjects', savedBookFromSubjects);
    }
    @Action actSavedBookShown(isSavedBookShown: boolean) {
        this.context.commit('setSavedBookShown', isSavedBookShown);

        if (!isSavedBookShown) {
            this.context.commit('setSavedBookFromSubjects', {});
        }
    }
    @Action async GET_fromShelf(calledfromPagination) {
        if (calledfromPagination) {
            this.context.dispatch('GET_ShelvesList');
        }

        this.context.rootState.loader.Loader = true; // change
        let shelfID = this.getCurrentShelf;
        const AccessToken = `Bearer ${localStorage.access_token}`;
        let startIndex;
        (this.currentPage > 1) ? startIndex = (this.currentPage + 3) || 0 : startIndex = 0;
        const maxResults = 5;

        return await axios
            .get(`${this.Params.proxyurl}${this.Params.URL}/${shelfID}/volumes?country=UA&startIndex=${startIndex}&maxResults=${maxResults}&key=${this.Params.API_KEY}`, {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json;charset=UTF-8",
                    'Authorization': AccessToken,
                }
            })
            .then(response =>  {
                this.context.rootState.loader.Loader = false; // change
                this.context.commit('setShelfArray', response.data.items);
                let totalItems: any = this.getCurrentShelfPagesCount;

                let paginationLength;
                (Number(totalItems) < 5) ? paginationLength = 1 : paginationLength = (totalItems / 5).toFixed();
                this.context.dispatch('actPaginationLength', paginationLength);
                return response
            })
            .catch(error => {
                if (error.response) {
                    return error.response;
                }
            });
    }
    @Action async GET_ShelvesList() {
        const AccessToken = `Bearer ${localStorage.access_token}`;

        return await axios
            .get(`${this.Params.proxyurl}${this.Params.URL}?key=${this.Params.API_KEY}`, {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json;charset=UTF-8",
                    'Authorization': AccessToken,
                }
            })
            .then(response =>  {
                this.context.rootState.loader.Loader = false; // change
                const CurrentShelf = response.data.items.filter((shelf: any) => shelf.id == this.getCurrentShelf);
                this.context.commit('setCurrentShelfAccess', CurrentShelf[0].access);
                this.context.commit('setShelvesList', response.data.items);
                this.context.commit('setCurrentShelfPagesCount', CurrentShelf[0].volumeCount);
                return response
            })
            .catch(error => {
                if (error.response) {
                    return error.response;
                }
            })
    }
    @Action actAddToShelf(addTo: any) {
        const {bookID, shelfID, isSubjectsPage, book} = addTo;
        this.context.rootState.loader.Loader = true;
        const AccessToken = `Bearer ${localStorage.access_token}`

        axios
            .post(`${this.Params.proxyurl}${this.Params.URL}/${shelfID}/addVolume?volumeId=${bookID}&country=UA&key=${this.Params.API_KEY}`, {}, {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json;charset=UTF-8",
                    'Authorization': AccessToken,
                }
            })
            .then(response =>  {
                this.context.rootState.loader.Loader = false;
                this.context.dispatch('GET_fromShelf');
                if (response.statusText === "OK" && isSubjectsPage) {
                    this.context.dispatch('actSavedBookFromSubjects', book);
                    this.context.dispatch('actSavedBookShown', true);
                }
            })
    }
    @Action actDeleteFromShelf(bookID: string) {
        this.context.rootState.loader.Loader = true;
        const shelf = this.getCurrentShelf;
        const AccessToken = `Bearer ${localStorage.access_token}`

        axios
            .post(`${this.Params.proxyurl}${this.Params.URL}/${shelf}/removeVolume?volumeId=${bookID}&country=UA&key=${this.Params.API_KEY}`, {}, {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json;charset=UTF-8",
                    'Authorization': AccessToken,
                }
            })
            .then(() =>  {
                this.context.rootState.loader.Loader = false;
                this.context.dispatch('GET_fromShelf');
            })
    }
    @Action actCurrentShelf(shelfId: any) {
        const ShelfID = Number(shelfId);
        this.context.commit('setCurrentShelf', ShelfID);
        return this.context.dispatch('GET_fromShelf');
    }
    @Action actCurrentShelfName(shelfName: any) {
        this.context.commit('setCurrentShelfName', shelfName);
    }
    @Action async actHaveReadBooksNumber() {
        const AccessToken = `Bearer ${localStorage.access_token}`
        const shelfID = 4;
        const maxResults = 4000;

        return await axios
            .get(`${this.Params.proxyurl}${this.Params.URL}/${shelfID}/volumes?country=UA&maxResults=${maxResults}&key=${this.Params.API_KEY}`, {
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Content-Type": "application/json;charset=UTF-8",
                    'Authorization': AccessToken,
                }
            })
            .then(response =>  {
                if(response.data.items) {
                    const HaveReadBooksArray = response.data.items;
                    this.context.commit('setHaveReadBooksNumber', response.data.items.length);
                    let HaveReadPages = 0;
                    HaveReadBooksArray.forEach((Book: any) => {
                        HaveReadPages += Book.volumeInfo.pageCount || 0;
                    });
                    this.context.commit('setHaveReadPagesNumber', HaveReadPages);
                }
                return response
            })
            .catch((error) => {
                if (error.response) {
                    return error.response;
                }
            });
    }
    @Action actCurrentPage(currentPage: any) {
        this.context.commit('setCurrentPage', currentPage);
    }
    @Action actPaginationLength(paginationLength: number) {
        this.context.commit('setPaginationLength', +paginationLength);
    }
    @Action actCurrentShelfPagesCount(pagesCount: number) {
        this.context.commit('setCurrentShelfPagesCount', pagesCount);
    }
    @Action async actHomeMainRequest(shelfID: number) {
        const readedBooks = this.context.dispatch('actHaveReadBooksNumber');
        const shelvesList = this.context.dispatch('GET_ShelvesList');
        const getCurrentShelf = this.context.dispatch('actCurrentShelf', shelfID);

        return await Promise.all([readedBooks, shelvesList, getCurrentShelf])
            .then(results => {
                console.log(results)
                results.forEach((result: any) => {
                    if (result.status && result.status === 401 || 403) {
                        router.push("/login");
                    }
                });
                if (results[0].data && results[0].data.error && results[0].data.error.message) {
                    alert(results[0].data.error.message)
                }
                this.context.rootState.loader.Loader = false;
            }, reason => {
                console.log("allSettled reason", reason);
                this.context.rootState.loader.Loader = false;
            })
            .catch((error) => {
                console.log("allSettled error", error)
                this.context.rootState.loader.Loader = false;
            });
    }
}
export default GoogleBooksAPI;