import { Component, Vue } from "vue-property-decorator";
const { GoogleLogin, LoaderPlugin } = require('vue-google-login');

import { namespace } from 'vuex-class';
const SubjectsModule = namespace('subjects');
const GoogleBooksAPI = namespace('GoogleBooksAPI');

Vue.use(LoaderPlugin, {
  client_id: "545324358636-40oml219h11b1st6aitu67ub5pd6sm9l.apps.googleusercontent.com"
});

@Component({
  components: { GoogleLogin }
})
export default class Menuleft extends Vue {

  params: any = {
    client_id: "545324358636-40oml219h11b1st6aitu67ub5pd6sm9l.apps.googleusercontent.com"
  }

  user = Object;

  @GoogleBooksAPI.Getter private getHaveReadBooksNumber!: any;
  @GoogleBooksAPI.Getter private getHaveReadPagesNumber!: any;

  @SubjectsModule.Getter private SubjectsArray: any;

  drawer = null;
  right: boolean = false;

  Logout() {
    localStorage.removeItem('User');
    localStorage.removeItem('access_token');
    this.$router.push({ path: '/login', name: 'Login' });
  }
  beforeMount() {
    this.user = JSON.parse(localStorage.getItem('User') || '{}');
  }
}

