import { Vue, Component, Prop } from 'vue-property-decorator';

import { namespace } from 'vuex-class';
const HomeModule = namespace('home');
const GoogleBooksAPI = namespace('GoogleBooksAPI');

@Component
export default class Button extends Vue {

  @GoogleBooksAPI.Getter private getCurrentShelf: any;
  @GoogleBooksAPI.Action('actAddToShelf') actAddToShelf: any;
  @GoogleBooksAPI.Action('actDeleteFromShelf') actDeleteFromShelf: any;

  @HomeModule.Action('actDeleteBookFromSearchResults') actDeleteBookFromSearchResults: any;

  @Prop(String) action: any;
  @Prop(Object) book: any;

  addtobook() {
    this.actAddToShelf({bookID: this.book.id, shelfID: this.getCurrentShelf});
    this.actDeleteBookFromSearchResults(this.book.id);
  }
  deleteBook() {
    this.actDeleteFromShelf(this.book.id);
  }
}
