import { Module, VuexModule, Mutation, Action } from 'vuex-module-decorators'
import axios from 'axios'

@Module({ namespaced: true })

class HomeModule extends VuexModule {

    requestedBooks: any = [];
    isPossibleActive: boolean = false;
    searchPhrase: string = '';

    get RequestedBooks() { return this.requestedBooks }
    get PossibleState() { return this.isPossibleActive }
    get getSearchPhrase() { return this.searchPhrase }

    @Mutation setBooks(payload: any) {
        this.requestedBooks = payload
    }
    @Mutation setPossible(payload: any) {
      this.isPossibleActive = payload
    }
    @Mutation setSearchPhrase(payload: any) {
      this.searchPhrase = payload
    }
    @Mutation ClearRequestedBooks() {
      this.requestedBooks = [];
    }
    @Mutation deleteBookFromSearchResults(bookID: number) {
      const newRequestedArr = this.requestedBooks.filter((elem: any) => elem.id !== String(bookID));
      this.requestedBooks = newRequestedArr;
    }

    @Action searchForBooks(bookTitle: string) {
        this.context.dispatch('actSearchPhrase', bookTitle);
        this.context.rootState.loader.Loader = true;
        const URL = `https://www.googleapis.com/books/v1/volumes/?q=`;
        const maxResults = 40;
        axios
          .get(`${URL}${bookTitle}&maxResults=${maxResults}`)
          .then(response =>  {
            this.context.rootState.loader.Loader = false;
            this.context.commit('setBooks', response.data.items);
          });
    }
    @Action findBySubject(findBooks: any) {
      const { subj, lang } = findBooks;
      const maxResults = 20;
      const URL = `https://www.googleapis.com/books/v1/volumes?q=subject:${subj}&maxResults=${maxResults}&langRestrict=${lang}`;

      axios
        .get(`${URL}`)
        .then(response =>  {
          this.context.commit('setBooks', response.data.items);
        })
        .catch(error => {
          console.log(error);
          alert('Error in HomeModule --"findBySubject" ')
        });
    }
    @Action actPossibleState(bool: boolean) {
      this.context.commit('setPossible', bool)
    }
    @Action actSearchPhrase(searchPhrase: string) {
      this.context.commit('setSearchPhrase', searchPhrase)
    }
    @Action actClearRequestedBooks() {
      this.context.commit('ClearRequestedBooks');
    }
    @Action actDeleteBookFromSearchResults(bookID: number) {
      this.context.commit('deleteBookFromSearchResults', bookID);
    }
}
export default HomeModule;