import ButtonComponent from '@/components/Button/index.vue';
import { Vue, Component, Prop } from 'vue-property-decorator';

import { namespace } from 'vuex-class';
const HomeModule = namespace('home');
const GoogleBooksAPI = namespace('GoogleBooksAPI');

declare interface book {
  volumeInfo: any
}
@Component({
  components: { ButtonComponent }
})

export default class Book extends Vue {

  data() {
      return {
        title: this.book.volumeInfo.title,
        imageLinks: this.book.volumeInfo.imageLinks,
        pages: this.book.volumeInfo.pageCount,
        description: this.book.volumeInfo.description,
        authors: this.book.volumeInfo.authors,
        published: this.book.volumeInfo.publishedDate,
        categories: this.book.volumeInfo.categories,
        previewLink: this.book.volumeInfo.previewLink,
      }
  }

  @GoogleBooksAPI.Getter private getCurrentShelfAccess: any;
  @GoogleBooksAPI.Getter private getCurrentShelf: any;
  @GoogleBooksAPI.Action('actAddToShelf') actAddToShelf: any;

  @HomeModule.Getter private PossibleState: any;
  @HomeModule.Action('actPossibleState') actPossible: any;
  @HomeModule.Action('actDeleteBookFromSearchResults') actDeleteBookFromSearchResults: any;


  @Prop(Object) book: any;
  @Prop(String) btnAction: any;
  @Prop(String) access: any;
  @Prop(Boolean) move: any;

  isBookActive: any = false;
  getRandomGradient() {
    return `linear-gradient(#${Math.floor(Math.random() * 16777215).toString(16)}40 , rgba(0, 0, 0, 0))`
  }
  getRandomColor() {
    return `#${Math.random().toString(16).substr(2,6)}`
  }
  bookToggle() {
    this.isBookActive = !this.isBookActive;
  }
  openCategory(category: string) {
    console.log(category)
  }
  addBook() {
    this.actAddToShelf({bookID: this.book.id, shelfID: this.getCurrentShelf});
    this.actDeleteBookFromSearchResults(this.book.id);
  }
  mouseDown(event: any) {
    event.stopPropagation();
    const self = this;
    let book = this.$refs.book as HTMLElement;
    const startX = book.getBoundingClientRect().left;
    book.style.position = 'absolute';
    moveAt(event);
    book.style.zIndex = "1000";

    function moveAt(e: any) {
      book.style.left = e.pageX - book.offsetWidth + 'px';
      book.style.top = e.pageY - 250 + 'px';
    }
    document.onmousemove = function() {
      moveAt(event);
    }
    book.onmouseup = function() {
      self.bookToggle(); // close book if it's open
      document.onmousemove = null;
      book.onmouseup = null;
      const endX = book.getBoundingClientRect().left;
      const newShiftX = endX - startX;
      book.style.position = 'static';
      self.actPossible(false); // hide dashed border
      if ( newShiftX > 200 ) { self.addBook(); }
    }
    book.ondragstart = function() {
      return false;
    };
    this.actPossible(true);
  }
}
