import { Module, VuexModule, Mutation, Action } from 'vuex-module-decorators';
import axios from 'axios';

@Module({ namespaced: true })
class SubjectsModule extends VuexModule {

    Subject = [];

    Art = [];
    Fantasy = [];
    Fiction = [];
    Romance = [];
    Inspirational = [];
    Business = [];
    History = [];
    Mind = [];
    Self = [];
    Sports = [];

    Subjects = [
      'Art', 'Fantasy', 'Fiction',
      'Romance', 'Inspirational',
      'Business', 'History', 'Mind',
      'Self', 'Sports'
    ]

    get SubjectArr() { return this.Subject }
    get SubjectsArray() { return this.Subjects }

    get getArt() { return this.Art }
    get getFantasy() { return this.Fantasy }
    get getFiction() { return this.Fiction }
    get getRomance() { return this.Romance }
    get getInspirational() { return this.Inspirational }
    get getBusiness() { return this.Business }
    get getHistory() { return this.History }
    get getMind() { return this.Mind }
    get getSelf() { return this.Self }
    get getSports() { return this.Sports }

    @Mutation setSubject(payload: any) {
       this.Subject = payload
    }
    @Mutation SetSubject(subjectObj: any) {
      const { subjectName, subjectArray } = subjectObj;
      switch(subjectName) {
        case 'Art': this.Art = subjectArray; break;
        case 'Fantasy': this.Fantasy = subjectArray; break;
        case 'Fiction': this.Fiction = subjectArray; break;
        case 'Romance': this.Romance = subjectArray; break;
        case 'Inspirational': this.Inspirational = subjectArray; break;
        case 'Business': this.Business = subjectArray; break;
        case 'History': this.History = subjectArray; break;
        case 'Mind': this.Mind = subjectArray; break;
        case 'Self': this.Self = subjectArray; break;
        case 'Sports': this.Sports = subjectArray; break;
      }
    }
    @Mutation clearSubjectsArr() {
      this.Art = [];
      this.Fantasy = [];
      this.Fiction = [];
      this.Romance = [];
      this.Inspirational = [];
      this.Business = [];
      this.History = [];
      this.Mind = [];
      this.Self = [];
      this.Sports = [];
    }

    @Action actSubject(subjectObj: any) {
        const { sub, lang } = subjectObj;
        const maxResults = 12;
        const URL = `https://www.googleapis.com/books/v1/volumes?q=subject:${sub}&maxResults=${maxResults}&langRestrict=${lang}`;
        axios
          .get(`${URL}`)
          .then(response =>  {
          console.log(response.data);
              this.context.commit('setSubject', response.data.items);
          });
    }
    @Action MultipleRequest(lang: any) {
      this.context.rootState.loader.Loader = true; // change

      lang = lang || 'en';
      let promises: any = [];
      this.SubjectsArray.forEach(subject => {
        let URL = `https://www.googleapis.com/books/v1/volumes?q=subject:${subject}&maxResults=36&langRestrict=${lang}`;
        promises.push(axios.get(URL));
      });
        axios.all(promises).then(axios.spread((...responses) => {

        this.context.rootState.loader.Loader = false; // change

        const responseArt: any = responses[0]
        const responseFantasy: any = responses[1]
        const responseFiction: any = responses[2]
        const responseRomance: any = responses[3]
        const responseInspirational: any = responses[4]
        const responseBusiness: any = responses[5]
        const responseHistory: any = responses[6]
        const responseMind: any = responses[7]
        const responseSelf: any = responses[8]
        const responseSports: any = responses[9]

        this.context.commit('SetSubject', { subjectName: 'Art', subjectArray: responseArt.data.items });
        this.context.commit('SetSubject', { subjectName: 'Fantasy', subjectArray: responseFantasy.data.items });
        this.context.commit('SetSubject', { subjectName: 'Fiction', subjectArray: responseFiction.data.items });
        this.context.commit('SetSubject', { subjectName: 'Romance', subjectArray: responseRomance.data.items });
        this.context.commit('SetSubject', { subjectName: 'Inspirational', subjectArray: responseInspirational.data.items });
        this.context.commit('SetSubject', { subjectName: 'Business', subjectArray: responseBusiness.data.items });
        this.context.commit('SetSubject', { subjectName: 'History', subjectArray: responseHistory.data.items });
        this.context.commit('SetSubject', { subjectName: 'Mind', subjectArray: responseMind.data.items });
        this.context.commit('SetSubject', { subjectName: 'Self', subjectArray: responseSelf.data.items });
        this.context.commit('SetSubject', { subjectName: 'Sports', subjectArray: responseSports.data.items });
      })).catch(errors => {
        console.log(errors);
      })
    }
    @Action actClearSubjectsArr() {
      this.context.commit('clearSubjectsArr');
    }
}

export default SubjectsModule;