import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
const LoaderPlugin = require('vue-google-login');
import { Framework } from 'vue-google-login'

const HomeModule = namespace('home');
const GoogleBooksAPI = namespace('GoogleBooksAPI');

import Search from '@/components/Search/index.vue';
import Loader from '@/components/Loader/index.vue';
import Book from '@/components/Book/index.vue';
import BookSmall from '@/components/BookSmall/index.vue';
import ScrollToTopButton from '@/components/ScrollToTopButton/index.vue';
import Pagination from '@/components/Pagination/index.vue';

Vue.use(LoaderPlugin, {
    client_id: "545324358636-40oml219h11b1st6aitu67ub5pd6sm9l.apps.googleusercontent.com"
});

declare module 'vue/types/vue' {
  interface VueConstructor {
    GoogleAuth: Framework
  }
}

@Component({
  components: { Book, Search, ScrollToTopButton, Loader, BookSmall, Pagination }
})

export default class Home extends Vue {

  shelfValue = 2;
  selected = {
      id: 2
  }

  @GoogleBooksAPI.Getter private getShelfArray: any;
  @GoogleBooksAPI.Getter private getShelvesList: any;
  @GoogleBooksAPI.Getter private getCurrentShelfAccess: any;
  @GoogleBooksAPI.Getter private getCurrentShelf: any;

  @GoogleBooksAPI.Action('actHomeMainRequest') actHomeMainRequest: any;
  @GoogleBooksAPI.Action('actCurrentShelf') actCurrentShelf: any;
  @GoogleBooksAPI.Action('actCurrentShelfName') actCurrentShelfName: any;
  @GoogleBooksAPI.Action('actPaginationLength') actPaginationLength: any;

  @HomeModule.Getter private RequestedBooks: any;
  @HomeModule.Getter private PossibleState: any;
  @HomeModule.Getter private getSearchPhrase: any;
  @HomeModule.Action('searchForBooks') searchForBooks: any;

  // defaultShelf = { title: "Have read", id: 4 }
  defaultShelf = {
    id: Number
  };
  item = {};

  SelectShelf(shelf: any) {
    this.$router.push(`/${this.$route.name}?shelfID=${shelf.id}`);
    this.actCurrentShelf(shelf.id);
    this.actCurrentShelfName(shelf.title);
  }
  mounted() {
    this.defaultShelf.id = this.$route.query.shelfID || this.getCurrentShelf
  }
  beforeMount() {
    // const params: any = {
    //   client_id: "545324358636-40oml219h11b1st6aitu67ub5pd6sm9l.apps.googleusercontent.com"
    // }

    try {
      Vue.GoogleAuth.then(auth2 => {
          // auth2.signIn(params).then(GoogleUser => {
          //   let expireDate = GoogleUser.getAuthResponse(params).expires_at;
          //   var d = new Date(expireDate)
          //   console.log(d);
          // });

        if (auth2.isSignedIn.get()) {
          const shelfID = this.$route.query.shelfID || this.getCurrentShelf;

          if (shelfID) {
            this.actHomeMainRequest(shelfID);
          } else {
            this.$router.push('/Home?shelfID=4');
            this.actHomeMainRequest(4);
          }
        } else {
          this.$router.push({ path: '/login', name: 'Login' });
        }
      });
    } catch(e) {
      this.$router.push({ path: '/login', name: 'Login' });
    }
  }
}