import { Component, Vue, Prop } from "vue-property-decorator";

import { namespace } from 'vuex-class';
const GoogleBooksAPI = namespace('GoogleBooksAPI');

import ModalChooseShelf from '@/components/ModalChooseShelf/index.vue';

@Component({
  components: { ModalChooseShelf }
})
export default class Booksmall extends Vue {
  @Prop(Object) book: any;
  @Prop(String) access: any;
  @Prop(String) btnAction: any;

  data() {
    return {
      title: this.book.volumeInfo.title,
      imageLinks: this.book.volumeInfo.imageLinks,
      pages: this.book.volumeInfo.pageCount,
      description: this.book.volumeInfo.description,
      authors: this.book.volumeInfo.authors,
      published: this.book.volumeInfo.publishedDate,
      categories: this.book.volumeInfo.categories,
      previewLink: this.book.volumeInfo.previewLink,
      show: false
    }
  }

  @GoogleBooksAPI.Action('actDeleteFromShelf') actDeleteFromShelf: any;

  getRandomColor() {
    return `#${Math.floor(Math.random() * 16777215).toString(16)}40`
  }
  getRandomGradient() {
    return `linear-gradient(#${Math.floor(Math.random() * 16777215).toString(16)}40, rgba(0, 0, 0, 0))`
  }
  deleteBook() {
    this.actDeleteFromShelf(this.book.id);
  }
}

