
import { Component, Vue } from "vue-property-decorator";
import { namespace } from 'vuex-class';

const GoogleBooksAPI = namespace('GoogleBooksAPI');

@Component({
  components: {}
})
export default class Pagination extends Vue {

  length: number = 1;

  @GoogleBooksAPI.Getter private getPaginationLength: any;
  @GoogleBooksAPI.Getter private getCurrentPage: any;
  @GoogleBooksAPI.Action('actCurrentPage') actCurrentPage: any;
  @GoogleBooksAPI.Action('GET_fromShelf') GET_fromShelf: any;

  get currentPage() {
    return this.getCurrentPage
  }
  set currentPage(value: number) {
      if (this.getCurrentPage !== value) {
        this.actCurrentPage(value);
        this.GET_fromShelf(true);
      }
  }

  beforeMount() {
    this.actCurrentPage(1);
  }
}

