import { Vue, Component } from "vue-property-decorator";
const { GoogleLogin } = require('vue-google-login');

import { namespace } from 'vuex-class';
const GoogleBooksAPI = namespace('GoogleBooksAPI');

import MenuLeft from '@/components/MenuLeft/index.vue';

@Component({
    components: { GoogleLogin, MenuLeft }
})
export default class Header extends Vue {
    user = Object;

    @GoogleBooksAPI.Getter private getHaveReadBooksNumber!: any;
    @GoogleBooksAPI.Getter private getHaveReadPagesNumber!: any;

    params: any = {
        client_id: "545324358636-40oml219h11b1st6aitu67ub5pd6sm9l.apps.googleusercontent.com"
    }

    Logout() {
        localStorage.removeItem('User');
        this.$router.push({ path: '/login', name: 'Login' });
    }
    beforeMount() {
        this.user = JSON.parse(localStorage.getItem('User') || '{}');
    }
}
