
import {Component, Vue, Prop } from "vue-property-decorator";

import { namespace } from 'vuex-class';
const HomeModule = namespace('home');
const GoogleBooksAPI = namespace('GoogleBooksAPI');

@Component({
  components: {}
})

export default class Modalchooseshelf extends Vue {

  @Prop(Object) book: any;

  dialogm1: string = '';
  dialog: boolean = false;
  switch1: boolean = true;

  selected: any = [];

  @GoogleBooksAPI.Getter private getShelvesList: any;
  @GoogleBooksAPI.Action('GET_ShelvesList') GET_ShelvesList: any;
  @GoogleBooksAPI.Action('actAddToShelf') actAddToShelf: any;

  @HomeModule.Action('actDeleteBookFromSearchResults') actDeleteBookFromSearchResults: any;

  modalSaveTo() {
    if(this.selected.length) {
      this.actDeleteBookFromSearchResults(this.book.id);
      this.selected.forEach((shelfID: string) => {
        this.actAddToShelf({bookID: this.book.id, shelfID: shelfID});
      });
    }
  }
}

