
import { Component, Vue } from "vue-property-decorator";


import { namespace } from 'vuex-class';
const LoaderModule = namespace('loader');

@Component({
  components: {}
})
export default class Loader extends Vue {

  @LoaderModule.Getter private getLoaderState!: Object;
  @LoaderModule.Action('actChangeLoaderState') actChangeLoaderState: any;

  overlay = Boolean;

  OpenLoader() {
    this.actChangeLoaderState(true);
  }

}

